.cms-g
{
	max-width: 1000px;
	margin: 0 auto;
	width: 100%;
}

.f-toe
{
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	-o-text-overflow: ellipsis;
	-icab-text-overflow: ellipsis;
	-khtml-text-overflow: ellipsis;
	-moz-text-overflow: ellipsis;
	-webkit-text-overflow: ellipsis;
}

.f-btn:active:focus, .f-btn:focus
{
	outline: dotted thin;
	outline: -webkit-focus-ring-color auto 1px;
	outline-offset: -2px;
	outline: 0;
}

.f-btn:active
{
	background-image: none;
}

.f-btn
{
	display: inline-block;
	margin-bottom: 0;
	font-size: 1.6rem;
	font-weight: 400;
	line-height: 1.2;
	text-align: center;
	white-space: nowrap;
	background-image: none;
	border: 1px solid transparent;
	border-radius: 0;
	cursor: pointer;
	outline: 0;
	-webkit-appearance: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	-webkit-transition: background-color .3s ease-out,border-color .3s ease-out;
	transition: background-color .3s ease-out,border-color .3s ease-out;
}


@font-face
{
	font-family: 'iconfont';
	src: url('/fonts/iconfont.eot');
	src: url('/fonts/iconfont.eot?#iefix') format('embedded-opentype'), url('/fonts/iconfont.woff') format('woff'), url('/fonts/iconfont.ttf') format('truetype'), url('/fonts/iconfont.svg#iconfont') format('svg');
}

.font
{
	font-family: "iconfont" !important;
	font-size: 16px;
	font-style: normal;
	-webkit-font-smoothing: antialiased;
	-webkit-text-stroke-width: 0.2px;
	-moz-osx-font-smoothing: grayscale;
}




.set-hr(@width,@height,@background)
{
	width: @width;
	height: @height;
	background: @background;
}



@media @xxl-up
{
	.header
	{
		padding: 2.5rem 0;

		&-logo
		{
			padding: 1rem 0;

			& img
			{
				width: 85%;
			}
		}
	}
}

@media @xxl-only
{
	.header
	{
		padding: 2.5rem 0;

		&-logo
		{
			padding: 1rem 0;

			& img
			{
				width: 85%;
			}
		}
	}
}

@media @xl-only
{
	.header
	{
		padding: 2rem 0;

		&-logo
		{
			padding: 1rem 0;

			& img
			{
				width: 85%;
			}
		}
	}
}

@media @large-only
{
	.header
	{
		padding: 2rem 0;

		&-logo
		{
			padding: 1rem 0;

			& img
			{
				width: 85%;
			}
		}
	}
}

@media @medium-only
{
	.header
	{
		padding: 1.5rem 0;

		&-logo
		{
			position: absolute;
		}

		& img
		{
			width: 55%;
			padding-top: 1.5rem;
		}
	}
}

@media @small-only
{
	.header
	{
		padding: 1.5rem 0;

		& img
		{
			width: 50%;
		}

		& nav li
		{
			&:first-child
			{
				margin-top: 1.5rem;
			}

			&:hover
			{
				background: rgba(0,0,0,.1);
			}
		}

		&-logo-div
		{
			overflow: hidden;
		}
	}
}



@media only screen and (min-width:641px)
{
	.header
	{
		& ul
		{
			display: block;

			& > li
			{
				float: left;
			}
		}

		& nav
		{
			float: right;
			clear: none;
			margin: 0;
			margin-top: 5px;
		}
	}
}







.header
{
	width: 100%;
	float: left;
	background: @default-background-color;
	position: relative;

	& button
	{
		border: 0px;
		background: transparent;
		font-size: 21px;
		position: absolute;
		right: 10px;
		top: 10px;
		color: @default-text-color;
		padding: 0.2rem;
	}

	& ul
	{
		list-style-type: none;
		padding: 0;
		margin: 0;
	}

	& li
	{
		& a
		{
			position: relative;
			padding: 0 20px;
			font-size: 18px;
			color: @global-primary;
			overflow: hidden;
			-webkit-transition: .3s;
			transition: .3s;
			display: block;
			line-height: 50px;

			&:active, &:focus
			{
				outline: none;
			}

			&:hover
			{
				background: rgba(0,0,0,.1);
				color: #888;
			}
		}

		&.on a
		{
			color: @default-on-color !important;
		}
	}
}

.header-container
{
	margin-left: auto;
	margin-right: auto;
	width: 100%;
	max-width: 1100px;
}

.pull-left
{
	float: left;
}



.header-logo
{
	float: left;

	& img
	{
		float: left;
	}

	&-div
	{
		padding: 0 1rem;
	}
}

.header-cms-logo
{
	float: left;
	padding-top: 0.5rem;
	position: relative;

	img
	{
		display: block;
	}

	span
	{
		display: block;
		font-size: 1.2rem;
		padding-top: 0.3rem;
		padding-left: 0.3rem;
	}

	&:before
	{
		.set-hr(1px,92%,@default-border-color);
		position: absolute;
		content: "";
		top: 0.3rem;
		left: -2rem;
	}
}


.footer
{
	&-bj
	{
		position: relative;
		clear: both;
		color: #fff;

		&-info
		{
			position: absolute;
			top: 0px;
			left: 0px;
			width: 100%;
			text-align: center;
		}

		& h1
		{
			color: #fff;
			font-size: 4em;
			font-weight: 500;
			margin-top: 7%;
		}

		& h2
		{
			margin-top: 1rem;
			font-weight: 500;
			font-size: 2rem;
		}

		& img
		{
			width: 100%;
		}
	}

	&-link
	{
		background-color: #f4f4f5;
		clear: both;
		overflow: hidden;
		padding: 3rem;
		position: relative;

		& > a
		{
			position: absolute;
			right: 2rem;
			top: 2rem;
			cursor: pointer;
			background: #999;

			&:hover
			{
				background: rgba(0,0,0,.2);
			}
		}

		& h1
		{
			margin: 0px;
			font-weight: 500;
			font-size: 1.6rem;
			color: #999;
			text-align: center;


			& span
			{
				& a
				{
					color: #999;
					font-size: 1.6rem;


					&:hover
					{
						color: @default-on-color;
					}
				}
			}
		}
	}
}

.page
{
	clear: both;
	overflow: hidden;
	text-align: center;
}

.pagination
{
	width: 100%;

	& li
	{
		display: inline-block;
		border: 1px solid #ccc;
		margin: 0.8rem .5rem;
		padding: .1rem 1.2rem;
		height: 3rem;
		line-height: 2.3rem;

		& a
		{
			color: #3f3f3f;
			font-size: 1.3rem;
		}

		& span
		{
			color: #3f3f3f;
			font-size: 1.3rem;
		}

		&:hover
		{
			background: #ff6713;
			border-color: #fa7b35;
			cursor: pointer;

			& a
			{
				color: #ffff;
			}
		}

		&[class^=disabled]:hover
		{
			background: #fff;
		}
	}

	& li.disabled
	{
		border: none;
	}

	& li.active
	{
		border-color: #ff6713;
		border: 1px solid #ccc !important;

		& span
		{
			color: #ff6713;
		}
	}
}
