.index-download
{
	position: relative;
	clear: both;
	background-color: #3e4e5e;

	& > img
	{
		width: 100%;
		display: block;
	}

	&-info
	{
		position: absolute;
		width: 100%;
		text-align: center;

		& h1
		{
			color: #fff;
			margin: 0 0 1rem 0;
			font-weight: 500;
		}

		& h2
		{
			color: #fff;
			margin: 0 0 0.4rem 0;
			font-weight: 500;
		}
	}

	&-info-a
	{
		margin-top: 5rem;

		& a
		{
			font-weight: 600;
			color: #fff;
			margin: 0 .5rem;
			border-radius: 5px;
			letter-spacing: 3px;
			background: @default-on-color;
			border: 2px solid @default-on-color;

			&:last-of-type
			{
				border: 2px solid #fff;
				background: #3e4e5e;
			}
		}
	}

	&-fork
	{
		position: absolute;
		bottom: 8%;
		right: 4%;
		& em
		{
			margin-right: 1.5rem;
            float:left;
		}
        & em:nth-child(n+2){ margin-top:0.3rem;}
	}
}


.index-features
{
	padding-bottom: 2rem;
	overflow: hidden;

	&-item
	{
		margin-bottom: 4rem;

		& img
		{
			display: block;
			margin: auto;
			text-align: center;
			padding: 0 0.5rem;
			width: 45%;
		}

		& span:extend(.f-toe)
		{
			display: block;
			margin: auto;
			text-align: center;
			font-size: 1.6rem;
			width: 100%;
			padding: 0 1rem;
		}

		& strong:extend(.f-toe)
		{
			display: block;
			margin: auto;
			text-align: center;
			margin-top: 2rem;
			width: 100%;
			padding: 0 1rem;
			color: #000;
			font-size: 1.9rem;
		}
	}

	&-title
	{
		width: 100%;
		margin: 5rem 0;
		text-align: center;

		& h1
		{
			margin: 0px;
			font-size: 3.5rem;
		}

		& h2
		{
			margin: 0px;
			font-size: 1.8rem;
			font-weight: 500;
			color: #5c5959;
		}
	}
}

.index-computer
{
	width: 100%;
	clear: both;
	margin: auto;
	text-align: center;
	background-color: #f4f4f5;
	padding-bottom: 1rem;
	overflow: hidden;

	& h1
	{
		margin: 0;
		font-size: 3.5rem;
		margin: 5rem 0 0 0;
		display: inline-block;
		width: 100%;
		text-align: center;
	}

	& h2
	{
		margin: 0;
		font-size: 1.8rem;
		font-weight: 500;
		text-align: center;
		color: #5c5959;
	}

	& img
	{
		display: block;
		width: 50%;
		margin: 4rem auto;
	}
}

.index-partner
{
	overflow: hidden;
	padding-bottom: 1rem;

	& > div
	{
		margin-top: 3rem;
	}

	& h1:extend(.index-computer h1)
	{
	}

	& h2:extend(.index-computer h2)
	{
	}

	&-div
	{
		height: 100px;
		overflow: hidden;
		display: table-cell;
		vertical-align: middle;
		text-align: center;

		& img
		{
			width: 60%;
		}
	}
}
