@media @small-only
{

	.about-info-div > div
	{
		padding: 0 0.5rem !important;
		margin-bottom: 1rem !important;
	}

	.about-banner img
	{
		height: 20rem;
	}

	.about-banner-div
	{
		width: 80% !important;
		left: 10% !important;

		&-border
		{
			border: 2px solid rgba(255,255,255,0.5) !important;
			border-top: none !important;
			height: 8rem !important;

			&::before
			{
				width: 25% !important;
				height: 2px !important;
			}

			&::after
			{
				width: 25% !important;
				height: 2px !important;
			}
		}


		&-span
		{
			height: 8rem !important;

			&-div
			{
				& strong
				{
					top: -1.2rem !important;
					width: 50% !important;
					left: 25% !important;
					font-size: 1.8rem !important;
				}

				& span
				{
					margin-top: 2.3rem;
					letter-spacing: 0.2rem;
					font-size: 1.4rem !important;
					padding: 0 2rem;
					width: 100%;
					white-space: nowrap;
					text-overflow: ellipsis;
					overflow: hidden;
					font-weight: 500 !important;
				}
			}
		}
	}

	.about-list
	{
		& div
		{
			border: none;
		}

		&-item-div span
		{
			width: 10rem !important;
			height: 10rem !important;
			line-height: 10rem !important;

			& i
			{
				font-size: 5rem !important;
			}
		}
	}

	.about-info-title
	{
		& h1
		{
			font-size: 2rem !important;
		}

		& h2
		{
			font-size: 1.6rem !important;
		}

		& h3
		{
			font-size: 1.6rem !important;
			font-weight: 500 !important;
		}
	}
}


.about-banner
{
	position: relative;
	clear: both;
	width: 100%;
	overflow: hidden;
	margin: auto;
	text-align: center;

	&-div
	{
		position: absolute;
		bottom: 20%;
		left: 30%;
		width: 40%;
		margin: auto;

		&-border
		{
			position: relative;
			border: 4px solid rgba(255,255,255,0.5);
			border-top: none;
			width: 100%;
			height: 10rem;

			&::before
			{
				position: absolute;
				content: "";
				width: 35%;
				background: rgba(255,255,255,0.5);
				height: 4px;
				left: 0px;
				top: 0px;
			}

			&::after
			{
				position: absolute;
				content: "";
				width: 35%;
				background: rgba(255,255,255,0.5);
				height: 4px;
				right: 0px;
				top: 0px;
			}
		}

		&-span
		{
			position: absolute;
			bottom: 0%;
			width: 100%;
			margin: auto;
			height: 10rem;

			&-div
			{
				position: relative;
				width: 100%;
				height: 100%;

				& strong
				{
					position: absolute;
					top: -2.2rem;
					width: 40%;
					left: 30%;
					font-size: 2.8rem;
					color: #dae4e7;
					letter-spacing: 0.5rem;
				}

				& span
				{
					position: absolute;
					color: #d5e5ea;
					font-size: 2rem;
					width: 100%;
					top: 10%;
					left: 0;
					text-align: center;
					margin-top: 2.3rem;
					font-weight: 600;
					letter-spacing: 0.2rem;
				}
			}
		}
	}
}

.about-info-title
{
	& h1
	{
		margin: 0px;
		font-weight: 500;
		padding: 0 1rem;
		font-size: 2.5rem;
	}

	& h2
	{
		margin: 0px;
		font-weight: 500;
		padding: 0 1rem 0.5rem 1rem;
		margin-bottom: 3rem;
		position: relative;
		font-size: 1.8rem;

		&::before
		{
			position: absolute;
			bottom: 0px;
			left: 1rem;
			content: "";
			.set-hr(8rem,2px,#0076ca);
		}
	}

	& h3
	{
		margin: 0px;
		font-weight: 500;
		padding: 1rem 0;
		color: #615e5e;
		padding: 0 1rem;
		font-size: 1.7rem;
	}
}


.about-info
{
	clear: both;
	overflow: hidden;
	padding: 2rem 0;
    &-title{
      margin-top:2rem;
    }
	&-div
	{
		margin: 2rem 0;
		overflow: hidden;

		& > div
		{
			padding: 0px;
			overflow: hidden;
			padding-left: 1rem;
			padding-right: 1rem;
			margin-bottom: 2rem;
		}

		&-img
		{

			& img
			{
				width: 100%;
			}
		}
	}
}

.about-list
{
	clear: both;
	overflow: hidden;

	& h1:extend(.about-info-title h1)
	{
	}

	& h2:extend(.about-info-title h2)
	{
		&::before:extend(.about-info-title h2::before)
		{
		}
	}

	& div:last-child > div
	{
		border: none;
	}

	&-item
	{
		border-right: 1px solid #d2d2d2;
		margin-bottom: 1rem;

		&-div
		{
			text-align: center;

			& span
			{
				width: 12rem;
				height: 12rem;
				line-height: 12rem;
				border-radius: 50%;
				border: 1px solid #c6c6c6;
				display: block;
				text-align: center;
				margin: auto;

				& i
				{
					font-size: 5.5rem;
					color: #333;
				}
			}
		}

		& > span
		{
			display: block;
			text-align: center;
			color: #666;
			font-size: 1.4rem;
			margin-bottom: 0.4rem;
		}

		& > strong
		{
			display: block;
			text-align: center;
			font-size: 2rem;
			margin-top: 1rem;
			font-weight: 500;
			color: #000;
		}

		& > em
		{
			display: block;
			text-align: center;
			font-style: normal;
			font-size: 1.4rem;
			color: #333;
			margin-bottom: 1.2rem;
		}
	}
}

.about-bottom
{
	padding: 3rem 0;
	clear: both;
	overflow: hidden;

	& h1:extend(.about-info-title h1)
	{
	}

	& h2:extend(.about-info-title h2)
	{
		&::before:extend(.about-info-title h2::before)
		{
		}
	}

	& h3:extend(.about-info-title h3)
	{
	}
}
