@media @large-only
{
	.service-banner-div
	{
		&-logo
		{
			padding: 4% !important;
		}

		&-img img
		{
			width: 40% !important;
			left: 30% !important;
		}
	}
}

@media @medium-only
{

	.service-banner
	{
		& > img
		{
			height: 38rem;
		}

		&-div
		{
			&-logo
			{
				padding: 4% !important;

				& strong
				{
					font-size: 4rem !important;
				}

				& span
				{
					font-size: 1.6rem !important;
				}
			}

			&-img img
			{
				width: 40% !important;
				left: 30% !important;
			}
		}
	}

	.service-list-didt
	{
		padding: 1rem 0 0.5rem !important;

		& span
		{
			width: 7rem !important;
			height: 7rem !important;
			line-height: 7rem !important;

			& i
			{
				font-size: 3.5rem !important;
			}
		}

		& strong
		{
			font-size: 1.4rem;
			font-weight: 500;
			padding: 1rem 0.2rem 0 0.2rem !important;
		}
	}

	.service-list-didb span
	{
		line-height: 2.1rem !important;
		font-size: 1.3rem !important;
		display: inline-block;
	}
}

@media @small-only
{

	.service-banner
	{
		& > img
		{
			height: 34rem;
		}

		&-div
		{
			&-logo
			{
				padding: 4rem 1rem 2rem 1rem !important;

				& strong
				{
					font-size: 2rem !important;
				}

				& span
				{
					font-size: 1.5rem !important;
				}

				& img
				{
					width: 50%;
				}
			}

			&-img img
			{
				width: 50% !important;
				left: 25% !important;
			}
		}
	}

	.am-u-sm-12
	{
		width: 100% !important;
	}

	.service-list-didb span i
	{
		font-style: normal;
		padding-right: 0.5rem;
		font-size: 1.7rem;
	}

	.service-bottom-item
	{
		margin-bottom: 2rem;
		overflow: hidden;

		& h1
		{
			font-size: 1.7rem !important;
			margin: 0 !important;
		}

		& span
		{
			font-size: 1.4rem !important;
			line-height: 2.3rem;
			display: inline-block;
			height: 6.6rem;
			overflow: hidden;
			text-overflow: ellipsis;
		}

		& img
		{
			margin-top: 1rem;
		}
	}

	.service-list
	{
		& h1
		{
			font-size: 2rem !important;
		}

		& h2
		{
			font-size: 1.6rem;
		}
	}
}

.service-banner
{
	clear: both;
	overflow: hidden;
	position: relative;
	text-align: center;

	& > img
	{
		width: 100%;
	}

	&-div
	{
		position: absolute;
		top: 0px;
		left: 0px;
		padding-left: 1.5rem;
		padding-right: 1.5rem;
		width: 100%;
		height: 100%;
		text-align: center;

		&-logo
		{
			height: 50%;
			padding-top: 8%;

			& img
			{
				display: block;
				margin: auto;
			}

			& em
			{
				display: block;
				font-style: normal;
				color: #ddf6fc;
				font-size: 1.6rem;
				padding-top: 0.5rem;
			}

			& strong
			{
				display: block;
				color: #ffb400;
				font-size: 5rem;
			}

			& span
			{
				display: block;
				color: #fff;
				font-size: 2rem;
				font-weight: bold;
			}
		}

		&-img
		{
			height: 50%;

			& > div
			{
				max-width: 550px;
				margin: auto;
				height: 100%;
				position: relative;
				overflow: hidden;

				& img
				{
					width: 70%;
					position: absolute;
					bottom: 0px;
					left: 15%;
				}
			}
		}
	}
}

.service-list
{
	padding: 6rem 0 4rem 0;
	overflow: hidden;
	clear: both;

	& h1
	{
		margin: 0px;
		text-align: center;
		font-size: 4rem;
	}

	&-d
	{
		margin-top: 3rem;
	}

	& h2
	{
		margin: 0px;
		text-align: center;
		color: #666;
		font-weight: 500;
		font-size: 1.6rem;
		padding: 0 1rem;
	}

	&-di
	{
		width: 20%;
		float: left;
	}

	&-didt
	{
		padding: 3rem 0 1.5rem 0;

		& span
		{
			display: block;
			text-align: center;
			width: 11rem;
			height: 11rem;
			border-radius: 50%;
			background-color: #fff;
			margin: auto;
			line-height: 11rem;

			& i
			{
				font-size: 6rem;
			}
		}

		& strong
		{
			text-align: center;
			display: block;
			color: #fff;
			padding-top: 2rem;
		}
	}

	&-didb span
	{
		font-size: 1.5rem;
		padding: 0.5rem 0.2rem 1rem 0.2rem;
		line-height: 2.3rem;
		display: inline-block;
	}

	&-d > div:nth-child(1) .service-list-didt
	{
		background: #ff5253;

		& i
		{
			color: #ff5253;
		}
	}

	&-d > div:nth-child(2) .service-list-didt
	{
		background: #f7b449;

		& i
		{
			color: #f7b449;
		}
	}

	&-d > div:nth-child(3) .service-list-didt
	{
		background: #75c273;

		& i
		{
			color: #75c273;
		}
	}

	&-d > div:nth-child(4) .service-list-didt
	{
		background: #4db5f7;

		& i
		{
			color: #4db5f7;
		}
	}

	&-d > div:nth-child(5) .service-list-didt
	{
		background: #7265ab;

		& i
		{
			color: #7265ab;
		}
	}
}

.service-flowchart
{
	clear: both;
	overflow: hidden;
	padding: 6rem 0 4rem 0;
	background-color: #f2f2f2;

	& h1:extend(.service-list h1)
	{
		color: #ca192b;
	}

	& h2:extend(.service-list h2)
	{
	}

	&-img
	{
		margin-top: 3rem;
		padding: 0 1rem;

		& img
		{
			width: 100%;
		}
	}
}

.service-bottom
{
	clear: both;
	overflow: hidden;
	padding: 6rem 0 4rem 0;

	&-item
	{
		& img
		{
			width: 100%;
			display: block;
			border: 2px solid #707070;
		}

		& h1
		{
			margin: 2rem 0.5rem 1rem 0.5rem;
			color: #d54856;
		}

		& span
		{
			font-size: 1.6rem;
		}
	}
}
